#skeleton-img,
.skeleton-text p {
    animation: skeleton-loading 1s linear infinite alternate;
}

#skeleton-img {
    height: 419px;
    width: 300px;
    border-radius: var(--form-border-radius);
}

#skeleton-article {
    height: 535px;
    width: 312px;
    border-radius: var(--form-border-radius);
    padding-block: 1rem;
}

.skeleton-text {
    display: grid;
    place-items: center center;
    padding: 1rem;

    & p {
        width: 100%;
        height: 2rem;
        border-radius: var(--form-border-radius);
    }
}

/* Media Queries */

@media (min-width: 768px) {

    #skeleton-article {
        width: 358px;
    }
}

@media (min-width: 1024px) {

    #skeleton-article {
        height: 480px;
        width: 475px;
    }
}